import router from './router'
import store from './store'
import NProgress from 'nprogress' // Progress 
import 'nprogress/nprogress.css'// Progress 
import { Message } from 'element-ui'
import { getToken } from '@/utils/auth' // cookietoken

const whiteList = ['/login'] // 
router.beforeEach((to, from, next) => { // router ,to=>from=>
  NProgress.start()
  if (getToken()) { //1.cookieTokenKey
    if (to.path === '/login') { //1-1.
      next({ path: '/' })
      NProgress.done() // if current page is dashboard will not trigger	afterEach hook, so manually handle it
    } else { // 1-2.
      if (store.getters.roles.length === 0) { //1-2-1.storeroles
        store.dispatch('GetInfo').then(res => { // GetInfoPromise
          next()
        }).catch((err) => { // cookietoken
          store.dispatch('FedLogOut').then(() => { // cookiecookietoken
            Message.error(err || 'Verification failed, please login again')
            next({ path: '/' }) //login
          })
        })
      } else {
        next()
      }
    }
  } else { //2.cookieTokenKey
    if (whiteList.indexOf(to.path) !== -1) {// 2-1.login
      next()
    } else {
      next(`/login?redirect=${to.path}`) // 2-2.,
      NProgress.done()
    }
  }
})

router.afterEach(() => {
  NProgress.done() // Progress
})
